.loading__main-container{
  width: 100%;
  height: 100vh;
  position: fixed;
  background: $dark-gray;
  transition: all ease .3s;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading__text{
  font-size: 1.33rem;
  color: white;
  text-shadow: 0 0 10px $light-gray;
}

.spinner {
  width: 80px;
  height: 80px;

  position: relative;
  margin: 0 auto;
  margin-bottom: 10px;
}

.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: $primary;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
  animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}

@-webkit-keyframes sk-bounce {
  0%, 100% { -webkit-transform: scale(0.0) }
  50% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bounce {
  0%, 100% { 
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
  } 50% { 
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}